import logo from './logo.svg';
import './App.css';
import React, { Component, useContext, useState } from 'react';
import { Navbar,Container,Nav,Jumbotron, Button, Form } from 'react-bootstrap';
import { Link, Route, Switch } from 'react-router-dom';
import axios from 'axios';
import Detail from './Detail.js';
import Data from './data.js';
import Cart from './Cart.js';

let 재고context = React.createContext();

function App() {

  let [shoes, shoes변경] = useState(Data);
  let [disable, setDisable] = useState(false);
  let [재고, 재고변경] = useState([10,11,12]);

  
 

  return (
    <div className="App">
        <Navbar bg="dark" variant="dark">
          <Container>
          <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link href="#home" as={Link} to="/" >Home</Nav.Link>
            <Nav.Link href="#detail" as={Link} to="/detail/0">Detail</Nav.Link>
            <Nav.Link href="#Cart" as={Link} to="/cart" >Cart</Nav.Link>
          </Nav>
          </Container>
        </Navbar>
        <Switch>
          <Route exact path="/">
            
            <Jumbotron className="background">
              <h1>20% Season Off</h1>
              <p>
                This is a simple hero unit, a simple jumbotron-style component for calling
                extra attention to featured content or information.
              </p>
              <p>
                <Button variant="primary">Learn more</Button>
              </p>
            </Jumbotron>

            <div className="container">

             
              <div className="row">
                {
                  shoes.map((a,i)=>{
                    return <Card shoes={shoes[i]} i={i}/>
                  })
                }
              </div>

              
            </div>

            <button disabled={disable} className="btn btn-primary" onClick={ (e)=>{ 
              
              
              axios.get('https://codingapple1.github.io/shop/data2.json')
                    .then( (result)=>{ 
                    console.log(result.data);
                    
                    shoes변경( [...shoes, ...result.data ] ); 
                    setDisable(true);
                          })
                    .catch(()=>{
                      console.log('실패했어요')
                    } ) }}>더보기</button>
                   
          </Route>


          <Route path="/detail/:id">
            <Detail shoes={shoes} 재고={ 재고 } 재고변경 = {재고변경} />
          </Route>

          <Route path="/cart">
            <Cart></Cart>
          </Route>
       </Switch>


    </div>
  );
}

function Card(props){

  let 재고 = useContext(재고context);

  return(
    <div className="col-md-4">
      <img src={'https://codingapple1.github.io/shop/shoes'+ (props.i+1) +'.jpg' }width="100%"/>
      <h4>{ props.shoes.title }</h4>
      <p>{ props.shoes.content } ＆ { props.shoes.price }</p>
     
    </div>
  )
}

export default App;
