import { useHistory, useParams  } from "react-router-dom";
import './Detail.scss';
import React, { useState, useEffect } from "react";
import { Navbar,Container,Nav,Jumbotron, Button, Form } from 'react-bootstrap';
import {CSSTransition} from 'react-transition-group';


function Detail(props){
    let [alert, alert변경] = useState(true);  
    let [누른탭, 누른탭변경] = useState(0);
    let [스위치, 스위치변경] = useState(false);

    useEffect(()=>{
        let 타이머 = setTimeout(()=>{ alert변경(false) },2000);
        return ()=>{ clearTimeout(타이머) }
    }, [ alert ]);
    

      
    let history = useHistory();
    let { id } = useParams();
    let 찾은상품 = props.shoes.find(function(상품){
        return 상품.id == id
    });
    return(
        <div className="container">
            <div className="박스">
                <h4 className="red 제목">Detail</h4>
            </div>

            {
                alert === true
                ?<div className="my-alert">
                    <p>재고가 얼마 남지 않았습니다</p>
                </div>
                :null
            }

            <div className="row">
              <div className="col-md-6">
                <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
              </div>
              <div className="col-md-6 mt-4">
                <h4 className="pt-5">{ 찾은상품.title }</h4>
                <p>{ 찾은상품.content }</p>
                <p>{ 찾은상품.price }원</p>


                <Info 재고={props.재고} />



                <button className="btn btn-danger" onClick={ ()=>{ props.재고변경([9,11,12]) } }>주문하기</button>
                {/* <button onClick={ ()=>{ history.goBack() } }  className="btn btn-danger">뒤로가기</button>  */}
                <button onClick={ ()=>{ history.push('/') } }  className="btn btn-danger">뒤로가기</button> 
              </div>
            </div>

            <div>
            <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
                <Nav.Item>
                    <Nav.Link eventKey="link-0" onClick={ ()=>{ 스위치변경(false); 누른탭변경(0) } }>Active</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={ ()=>{ 스위치변경(false); 누른탭변경(1) } }>Option 2</Nav.Link>
                </Nav.Item>
               
                
            </Nav>
            

            <div>
                <CSSTransition in={스위치} className="wow" timeout={500}>
                    <TabContent 누른탭={누른탭} 스위치변경={스위치변경} />  
                </CSSTransition>
            </div>
            
            </div>
          </div> 
    )
};


function TabContent(props){

    useEffect(
        ()=>{
            props.스위치변경(true);
        }
    );

    if(props.누른탭 === 0 ){
        return <div>내용0</div>
    }
    else if(props.누른탭 === 1){  
        return <div>내용1</div>
    }
   
}


function Info(props){
    return(
       <p>재고 : {props.재고[0]} </p>
    )
}

export default Detail;